<h2 id="introduction" tabindex="-1"><a class="header-anchor" href="#introduction" aria-hidden="true">#</a> Introduction</h2>
<p>In your daily work it is not uncommon that you find yourself carrying out the same task over and over again. After all, you have been employed for the specific skills you possess, and your employer wants you to make use of them. But in the long run, repeatedly doing the same thing over and over again can be quite boring, causing you to lose concentration and start making mistakes, which in turn makes you unproductive and lowers the quality of your work.</p>
<p>A computer is an excellent tool to save you from this. Once programmed, it will not only carry out the tasks for you, but it will do so without complaining, much faster than you can, and with the confidence that the task has been carried out correctly, since computers do not make mistakes.</p>
<p>Unfortunately, computers do not operate/think the same way as we humans do, so programming them can be quite tricky. In this course, you will get familiar with how computers work and how to program them using a programming language called <em>Python</em>. We will start with the very basics, and at the end of the course, you will be able to program computers on your own.</p>
<h3 id="pre-requisites" tabindex="-1"><a class="header-anchor" href="#pre-requisites" aria-hidden="true">#</a> Pre-requisites</h3>
<p>This course does not have any special pre-requisites other than basic math and English.</p>
