<h2 id="learning-activities" tabindex="-1"><a class="header-anchor" href="#learning-activities" aria-hidden="true">#</a> Learning Activities</h2>
<p>Here are short introduction to the learning activities offered in the course.</p>
<h3 id="recorded-lectures" tabindex="-1"><a class="header-anchor" href="#recorded-lectures" aria-hidden="true">#</a> Recorded Lectures</h3>
<p>In the beginning of each week you will be encouraged to view some recorded lectures. These will be quite theoretical and will teach you new things you can use when programming.</p>
<h3 id="tutorials" tabindex="-1"><a class="header-anchor" href="#tutorials" aria-hidden="true">#</a> Tutorials</h3>
<p>Each week you will also be encouraged to complete some optional exercises. This is for you to practice on using the theory you learned in the recorded lectures. In the beginning of each week there will be a tutorial where the teacher will show you and discuss sample answers to the exercises from the previous week.</p>
<h3 id="lab-sessions" tabindex="-1"><a class="header-anchor" href="#lab-sessions" aria-hidden="true">#</a> Lab Sessions</h3>
<p>Each week you may attend a 4 hours long lab session. At the lab session you may ask the teacher there for help with your laboratory work and the exercises. When you have completed your laboratory work you should also present your solution to a teacher at a lab session where you explain to the teacher how the code you have written works.</p>
<p>There are 3 different lab sessions each week, but you may only come to one. To distribute you, join one of the lab session groups on Canvas (<a href="https://ju.instructure.com/courses/4205/groups" target="_blank" rel="noopener noreferrer">TSPG17<OutboundLink/></a>, <a href="https://ju.instructure.com/courses/4199/groups" target="_blank" rel="noopener noreferrer">TSTG17<OutboundLink/></a>). First come, first served. It is OK to attend the sessions for the other groups too if there is room for it, but the students for those groups should of course be prioritized by the teacher.</p>
<h2 id="examination-tests" tabindex="-1"><a class="header-anchor" href="#examination-tests" aria-hidden="true">#</a> Examination Tests</h2>
<p>The course contains the two examination tests <code>Examination</code> and <code>Laboratory work</code>. To receive a final grade on the course you need to pass both examination tests. The final grade will be based on the grade for the <code>Examination</code> test.</p>
<h3 id="_1-examination" tabindex="-1"><a class="header-anchor" href="#_1-examination" aria-hidden="true">#</a> 1. Examination</h3>
<p>More information about the <code>Examination</code> test can be found on the page <RouterLink to="/courses/introduction-to-script-programming/written-examination/">Written Examination</RouterLink>.</p>
<h3 id="_2-laboratory-work" tabindex="-1"><a class="header-anchor" href="#_2-laboratory-work" aria-hidden="true">#</a> 2. Laboratory work</h3>
<p>More information about the <code>Laboratory work</code> test can be found on the page <RouterLink to="/courses/introduction-to-script-programming/laboratory-work/">Laboratory Work</RouterLink>.</p>
<h3 id="re-examination" tabindex="-1"><a class="header-anchor" href="#re-examination" aria-hidden="true">#</a> Re-examination</h3>
<FigureNumber /> below shows the study and exam periods during a year. The small green circle sectors shows when the (re-)examinations are offered in this course.
<AcademicYearFigure />
<p>Re-examination for the tests is offered at the re-exam periods in February and August. If there are some examination tests you haven't passed after that, you can re-take those tests the next time the course run.</p>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>The examination tests might have changed a little bit the next time the course run, and if you take some of them then, then you will take them according to the new description of the tests. You don't need to re-take an examination test you have already passed.</p>
</div>
